@import "variables";

%betterText {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

%h1 {
  @extend %betterText;

  font-family: $gilroy-700-bold, sans-serif;
  font-size: 98px;
  line-height: 96px;
  letter-spacing: -1.5px;
}

%h2 {
  @extend %betterText;

  font-family: $gilroy-700-bold, sans-serif;
  font-size: 60px;
  line-height: 72px;
  letter-spacing: -0.5px;
}

%h3 {
  @extend %betterText;

  font-family: $gilroy-700-bold, sans-serif;
  font-size: 48px;
  line-height: 60px;
  letter-spacing: 0;
}

%h4 {
  @extend %betterText;

  font-family: $gilroy-700-bold, sans-serif;
  font-size: 34px;
  line-height: 36px;
  letter-spacing: 0.25px;
}

%h5 {
  @extend %betterText;

  font-family: $gilroy-700-bold, sans-serif;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0.35px;
}

%h6 {
  @extend %betterText;

  font-family: $gilroy-700-bold, sans-serif;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.4px;
}

%s1 {
  @extend %betterText;

  font-family: $gilroy-600-semibold, sans-serif;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.18px;
}

%s2 {
  @extend %betterText;

  font-family: $gilroy-600-semibold, sans-serif;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.18px;
}

%p1 {
  @extend %betterText;

  font-family: $gilroy-500-medium, sans-serif;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.25px;
}

%p2 {
  @extend %betterText;

  font-family: $gilroy-500-medium, sans-serif;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.25px;
}

%p3 {
  @extend %betterText;

  font-family: $gilroy-500-medium, sans-serif;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.25px;
}

%overline {
  @extend %betterText;

  font-family: $gilroy-500-medium, sans-serif;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}

// Requires inline-block or block for proper styling
%truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
